* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: #EFEFEF;
}
/******* Blue speech bubble *******/

.speech-bubble {
  position: relative;
  background: #4788C8;
  margin: auto;
  width: 80%;
  padding: 0.5rem;
  overflow-wrap: break-word;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.speech-bubble::after {
  content: '';
  position: absolute;
  top: 100%;
  left: 0%;
  width: 0;
  height: 0;
  border: 40px solid transparent;
  border-top-color: #4788C8;
  border-left: 0;
}

.speech-bubble-text {
  color: white;
  font-size: 1.3rem;
  font-weight: 200;
  text-transform: uppercase;
  font-family: Arial, Helvetica, sans-serif;
  margin-left: 2rem;
  margin-top : 1rem;
}

.speech-bubble-text span {
  color: white;
  font-weight: 600;
  padding-left: 0.3rem;
  padding-right: 0.3rem;
  text-transform: uppercase;
  font-family: Arial, Helvetica, sans-serif;
  font-style: italic;
}

.speechBubble-btn {
  background: #E4E4EE;
  color: #414141;
  padding: 1rem 1.5rem;
  font-size: 1rem;
  text-align: center;
  border: none;
  cursor: pointer;
  bottom: 0.8rem;
  font-weight: 500;
  text-transform: uppercase;
  margin-right: 2rem;
}

/******* Header *******/

.header-background {
  background-color: #414141;
  border-bottom: 10px solid transparent;
  -webkit-border-image: url(/static/media/Border-BG.5b4e8615.jpg) 30 round;
          border-image: url(/static/media/Border-BG.5b4e8615.jpg) 30 round;
}

.header-container {
  background-color: #414141;
  display: flex;
  flex-wrap: wrap;
  /*flex-flow: row wrap*/
  justify-content: center;
  align-items: center;
  /* centering container*/
  width: 80%;
  margin: 0 auto;
}

.right-header {
  align-self: center;
  flex: 1 1;
}

.left-header {
  flex: 1 1;
}

#computerHeader {
  width: 35em;
}

.right-header p {
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.5rem;
  display: inline-block;
  text-transform: uppercase;
  font-weight: bold;
  margin: 1px auto;
  /*text-align: left;*/
  font-style: italic;
  letter-spacing: 0.5px;
  -webkit-transform: scaleY(1.2);
          transform: scaleY(1.2);
}

.right-header p span {
  font-weight: lighter;
  color: #B5B5B5;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.5rem;
  padding-left: 0.3rem;
  font-style: normal;
}

h4 {
  font-family: Arial, Helvetica, sans-serif;
  color: #B5B5B5;
  font-weight: lighter;
  margin : 1rem auto;
}

.learn-btn {
  background: #4788C8;
  color: #FFFFFF;
  margin : 1rem auto;
  padding: 1rem 2rem;
  font-size: 1rem;
  text-align: center;
  border: none;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 500;
  box-shadow: 5px 1px 5px -1px rgba(0, 0, 0, 0.19);
}

.learn-btn:hover {
  background: #FFFFFF;
  color: #4788C8;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
