  box-shadow: 5px 1px 5px -1px rgba(0, 0, 0, 0.19);
}

.tab-content {
  width: 100%;
  padding: 1em;
  order: 1;
  box-shadow: 5px 1px 5px -1px rgba(0, 0, 0, 0.19);
  background: #fff;
}

.tab-content button {
  background: #4788C8;
