}

#tabs input[type='radio'],
.tab-content {
  display: none;
}

#tabs input[type='radio']:checked+label {
  background: #fff;
  border-bottom: 1px solid #fff;
  box-shadow: 5px 1px 5px -1px rgba(0, 0, 0, 0.19);
}

